function ajaxLoadSingleFundInfo(){
	var deferred = $.Deferred();
  	var postdata = {};
  	postdata.fundId = filterXSS(sessionStorage.getItem("fundid"));
	$.ajax({
		url: Constants.getSingleFundInfoUrl(),
      	type:"GET",
      	data:postdata
  	}).done(function(response){
  		console.log("response",response);
  		var options = {
        whiteList: {
          strong: ['style'],
          p:"",
          br:"",
          font:['color'],
          b:"",
          i:""
        }
      };
		  var reportHtml = filterXSS(response.WorkCriticism,options);
		  $("#result-block").html(reportHtml);
		  deferred.resolve();
	});
	return deferred.promise();
}

$(document).on( "pageinit", "#page-sfund-report", function(event) {
	App.StartLoading();
  	$(".numura-warning").loadWarning();
  	$.when(ajaxLoadSingleFundInfo()).done(function(){
  		App.StopLoading();
  	});
});